import { useState, useEffect } from 'react';
import { LoadingButton } from '@mui/lab';
import {
  Card, CardHeader, CardContent,
  Button,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Divider,
  InputLabel, Select, MenuItem,
  Grid, TextField, RadioGroup, FormControlLabel, Radio, FormControl, FormLabel, fabClasses
}
  from '@mui/material';
import { TextareaAutosize } from '@mui/base';
import { Box, styled } from '@mui/system';
import { Paragraph } from 'app/components/Typography';
import useAuth from 'app/hooks/useAuth';
import axios from 'axios';
import { Formik, FieldArray } from 'formik';
import { NavLink, useNavigate, Link } from 'react-router-dom';
import * as Yup from 'yup';
import swal from 'sweetalert';
import { Breadcrumb } from "app/components";

const FlexBox = styled(Box)(() => ({ display: 'flex', alignItems: 'center' }));

const JustifyBox = styled(FlexBox)(() => ({ justifyContent: 'center' }));

const ContentBox = styled(JustifyBox)(() => ({
  height: '100%',
  padding: '32px',
  background: 'rgba(0, 0, 0, 0.01)',
}));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));

const TaxCategoryTable = styled(Table)(() => ({
  minWidth: 1500,
  whiteSpace: 'pre',
  '& small': {
    width: 50,
    height: 15,
    borderRadius: 500,
    boxShadow: '0 0 2px 0 rgba(0, 0, 0, 0.12), 0 2px 2px 0 rgba(0, 0, 0, 0.24)',
  },
  '& td': { borderBottom: 'none' },
  '& td:first-of-type': { paddingLeft: '16px !important' },
}));
// inital login credentials
const initialValues = {
  import_products: '',
};

// form field validation schema
const validationSchema = Yup.object().shape({
  // import_products: Yup.string()
  //   .required('Product Name is required!'),
});

const AddMedicinesStock = () => {
  const theme = useTheme();
  const { register } = useAuth();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [categories, setCategory] = useState(false);
  const [brands, setBrands] = useState(false);
  const [tax_categories, setTaxCategories] = useState(false);
  const [formData, setFormData] = useState(initialValues);
  const [importedData, setImportedData] = useState(false);



  // useEffect(() => {
  //   fetchData();
  // }, []);

  // /**
  //   * Fetching data from server.
  //   */
  // const fetchData = async () => {
  //   setLoading(true);
  //   try {
  //     await axios.get('/sanctum/csrf-cookie').then(response => {
  //       axios.post('/api/client/get-categories')
  //         .then(response => {
  //           if (response.data.response_code === 200) {
  //             setCategory(response.data.data);
  //           } else if (response.data.response_code === 401) {

  //           }
  //           setLoading(false);
  //         })
  //         .catch(function (error) {
  //           setLoading(false);
  //           console.error(error);
  //         });
  //       axios.post('/api/client/get-brands')
  //         .then(response => {
  //           if (response.data.response_code === 200) {
  //             setBrands(response.data.data);
  //           } else if (response.data.response_code === 401) {

  //           }
  //           setLoading(false);
  //         })
  //         .catch(function (error) {
  //           setLoading(false);
  //           console.error(error);
  //         });

  //       axios.post('/api/client/get-tax-categories')
  //         .then(response => {
  //           if (response.data.response_code === 200) {
  //             setTaxCategories(response.data.data);
  //           } else if (response.data.response_code === 401) {

  //           }
  //           setLoading(false);
  //         })
  //         .catch(function (error) {
  //           setLoading(false);
  //           console.error(error);
  //         });
  //     });
  //   } catch (e) {
  //     setLoading(false);
  //   }
  // }

  const handleChange = (e) => {
    console.log('dddddddd');
    console.log(e.target.name + '-' + e.target.value);
    setFormData({ ...formData, [e.target.name]: e.target.value });
    console.log(formData);
  };

  const changeHandler = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.files[0] });
  };

  const handleFormSubmit = (values, errors) => {
    setLoading(true);
    try {
      axios.get('/sanctum/csrf-cookie').then(response => {
        const uploadFormData = new FormData();
        for (let iKey in values) {
          if (iKey === 'import_products') {
            uploadFormData.append(iKey, formData[iKey])
          } else {
            if (values[iKey]) {
              uploadFormData.append(iKey, values[iKey])
            } else {
              uploadFormData.append(iKey, '')
            }
          }
        }
        const config = { headers: { "Content-Type": "multipart/form-data" } };
        axios.post('/api/client/get-excel-file-data', uploadFormData, config)
          .then(response => {
            if (response.data.response_code === 200) {
              setImportedData(response.data.data);
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              // navigate('/dashboard/medicine/list');
            } else if (response.data.response_code == 401 || response.data.response_code == '401') {
              errors.setErrors(response.data.errors);
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              // navigate('/dashboard/medicine/list');
            } else {
              errors.setErrors(response.data.errors);
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  const handleImportData = () => {
    try {
      console.log(importedData);
      axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/client/bulk-import-excel-file-data', {
          import_file_data: importedData
        })
          .then(response => {
            if (response.data.response_code === 200) {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
              navigate('/dashboard/medicine/list');
            } else if (response.data.response_code == 401 || response.data.response_code == '401') {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
            } else {
              swal({
                title: "Good job!",
                text: response.data.message,
                icon: "success",
              });
            }
            setLoading(false);
          })
          .catch(function (error) {
            console.error(error);
          });
      });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };

  return (
    <Container className="add-medicine">
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "Upload Bulk Medicine" }]} />
      </Box>
      <Card className="card" >
        <CardHeader
          title="Upload Bulk Medicine"
          titleTypographyProps={{
            variant: "h4",
            fontSize: '16px',
            fontWeight: '600',
            textTransform: 'uppercase',
            textAlign: 'center'
          }}
          action={
            <>
              <Button title="Back" component={Link}
                to={`/dashboard/medicine/list`}
                color="info" variant="outlined" size="small" sx={{ m: .5 }}>
                Download Simple Format
              </Button>
            </>
          }
        />
        <Divider />
        <CardContent>
          <Formik
            onSubmit={(values, errors) => {
              handleFormSubmit(values, errors);
            }
            }
            initialValues={initialValues}
            validationSchema={validationSchema}
            enableReinitialize={true}
          >
            {({ values, errors, touched, handleChange, handleBlur, handleSubmit }) => (
              <form onSubmit={handleSubmit}>

                <Grid container style={{ padding: '20px 0px' }} spacing={2}>
                  <Grid item sm={6} xs={12} lg={4}>
                    <label htmlFor="product_image" className='form-label'>Upload Excel Products File</label>
                    <FormControl fullWidth={true} sx={{ mb: 3 }}>
                      <input
                        id="import-products"
                        name="import_products"
                        type="file"
                        onChange={changeHandler}
                      />
                    </FormControl>
                    {(errors.import_products)
                      && (touched.import_products)
                      && <div style={{ color: '#ff3d57' }}>{`${errors.import_products}`}</div>}
                  </Grid>
                  <Grid item sm={6} xs={12} lg={2}>
                    <LoadingButton
                      fullWidth
                      type="submit"
                      color="primary"
                      loading={loading}
                      variant="contained"
                      sx={{ mb: 2, mt: 3 }}
                    >
                      Upload
                    </LoadingButton>
                  </Grid>
                </Grid>
              </form>
            )}
          </Formik>

          {importedData && (<>
            <Box overflow="auto">
              <TaxCategoryTable>
                <TableHead>
                  <TableRow>
                    <TableCell>Product Name</TableCell>
                    <TableCell>Category</TableCell>
                    <TableCell>Brand</TableCell>
                    <TableCell>Description</TableCell>

                    <TableCell>Unit Packing</TableCell>
                    <TableCell>Expiry Date</TableCell>
                    <TableCell>Available Quantity</TableCell>
                    <TableCell>HSN/SAC Code</TableCell>
                    <TableCell>Batch No.</TableCell>
                    <TableCell>MRP</TableCell>

                    <TableCell>Quantity</TableCell>
                    <TableCell>Discount</TableCell>
                    <TableCell>Bonus (Deal)</TableCell>
                    <TableCell>Trade Rate</TableCell>
                    <TableCell>Tax</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {importedData && importedData.map((row, index) => (
                    <TableRow key={index}>
                      <TableCell>{row.product_name}</TableCell>
                      <TableCell>{row.category}</TableCell>
                      <TableCell>{row.brand}</TableCell>
                      <TableCell>{row.description}</TableCell>
                      <TableCell>{row.unit_packing}</TableCell>
                      <TableCell>{row.expiry_date}</TableCell>
                      <TableCell>{row.available_qty}</TableCell>
                      <TableCell>{row.hsn_sac_code}</TableCell>
                      <TableCell>{row.batch_no}</TableCell>
                      <TableCell>{row.mrp}</TableCell>
                      <TableCell>{row.min_qty}</TableCell>
                      <TableCell>{row.min_qty_discount}</TableCell>
                      <TableCell>{row.min_qty_bonus_deal}</TableCell>
                      <TableCell>{row.min_qty_trade_rate}</TableCell>
                      <TableCell>{row.tax}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </TaxCategoryTable>
            </Box>
            <Grid container style={{ padding: '20px 0px' }} spacing={2}>

              <Grid item sm={6} xs={12} lg={2}>
                <LoadingButton
                  fullWidth
                  type="submit"
                  color="primary"
                  loading={loading}
                  variant="contained"
                  sx={{ mb: 2, mt: 3 }}
                  onClick={handleImportData}
                >
                  Import
                </LoadingButton>
              </Grid>
            </Grid>
          </>)}
        </CardContent>
      </Card>
    </Container >
  );
};

export default AddMedicinesStock;
